html {
  box-sizing: border-box;
}

* {
  user-select: none;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

.header h1 {
  margin: 0;
}

.container {
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 15px;
  max-width: 768px;
  height: 100vh;
}

.score-box {
  display: none;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 15px;
  margin: 0;
  padding: 15px;
  width: 100%;
  min-height: 50%;
  color: #ffffff;
  border-radius: 15px;
  background-color: #439181;
  box-shadow: 6px 6px #FABB6F;
}

.score-list {
  color: #FABB6F;
  font-size: 24px;
  text-shadow: 1px 1px #dacfcf;
}

.button {
  padding: 0;
  width: 220px;
  height: 55px;
  font-family: inherit;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 15px;
  background-color: #FABB6F;
  border: 0;
  box-shadow: 2px 2px 3px rgba(68, 68, 68, 0.2);
  transition: 0.3s;
  cursor: pointer;
}

.button:hover, 
.button:focus {
	box-shadow: 0 0 0 0.5em rgba(247, 233, 197, 0);
  color: #ffffff;
  border: 0;
  transition: color 0.5s;
	animation: pulse 1s;
}

@keyframes pulse{
	0%{box-shadow: 0 0 0 0 #FABB6F}
}

.back-btn {
  margin-bottom: 15px;
  color: #439181;
}

.box-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0;
  padding: 30px 15px;
  width: 100%;
  min-height: 45%;
  color: #ffffff;
  border-radius: 15px;
  background-color: #439181;
  box-shadow: 6px 6px #FABB6F;
}

.start-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.score-btn {
  margin-bottom: 15px;
  color: #439181;
  background-color: #FABB6F;
}

.start-btn {
  background-color: #BB8FCE;
}

.result {
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  font-size: 18px;
}

.card-box {
  display: none;
  align-content: center;
}

.check {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  min-height: 2%;
}

.check-box {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  width: 100px;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.time-box {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  width: 140px;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.step {
  font-size: 24px;
  color: #FABB6F;
  text-shadow: 1px 1px #fafafa;
}

.time {
  font-size: 24px;
  color: #FABB6F;
  text-shadow: 1px 1px #fafafa;
}

.statistics-box {
  display: none;
  flex-direction: column;
}

.statistics-box h2 {
  margin: 0 0 15px 0;
}

.restart-btn {
  margin-top: 15px;
  color: #ffffff;
  background-color: #BB8FCE;
}

.card-item {
  position: relative;
  width: 100px;
  height: 100px;
}

.disabled {
  pointer-events: none;
}

.item-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 40px;
  border-radius: 15px;
  box-shadow: 3px 4px 5px rgba(0, 0, 0, 0.2);
}

.item-front,
.item-back {
  position: absolute;
  backface-visibility: hidden;
  transition: transform 0.3s linear;
}

.item-front {
  color: #439181;
  background-color: #FABB6F;
  transform: perspective(600px) rotateY(0deg);
  cursor: pointer;
}

.item-back {
  color: #ffffff;
  background-color: #BB8FCE;
  transform: perspective(600px) rotateY(-180deg);
}

.item-back-active {
  box-shadow: 0 0 5px 2px rgba(61, 61, 61, 0.40);
	animation: glow 1s;
}

.item-back-active::after, 
.item-back-active::before {
  box-shadow: 0 0 5px 2px rgba(61, 61, 61, 0.40);
	animation: glow 1s;
}

@keyframes glow{
	0%{box-shadow: 0 0 12px 8px rgba(82, 243, 181, 0.397)}
}

.footer-description {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 230px;
  height: 24px;
  font-weight: 700;
}

.section-link {
  font-size: 20px;
  line-height: 20px;
  transition: 0.5s;
}

.section-link:hover {
  color: #FABB6F;
  transition: color 0.5s;
}

@media (max-width: 768px) {
  .container {
    padding: 0;
  }
  .box-wrapper {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin: 0;
    padding: 30px 15px;
    width: 100%;
    min-height: 100vh;
    color: #ffffff;
    border-radius: 0;
    box-shadow: none;
  }
}

@media (max-width: 400px) {
  .card-item {
    width: 80px;
    height: 80px;
  }
}
